.header {
  width: 100%;

  margin: 40px auto 0 auto;
  border-bottom: 1px solid rgba(54, 54, 54, 0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1023px) {
  .header {
    flex-direction: column;
    margin: 0 auto;
    justify-content: center;
  }
}
